.contact {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 80px;
    margin: 80px 120px;
}

.contact-title {
    position: relative;
    text-align: center;
}

.contact-title h1 {
    padding: 0px 30px;
    font-size: 80px;
    font-weight: 600;
}

.contact-title img {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
    height: 40px;
}

.contact-section {
    display: flex;
    gap: 150px;
    width: 100%;
    justify-content: space-between; /* Adjust alignment for wider screens */
}

.contact-left {
    display: flex;
    flex-direction: column;
    gap: 30px;
    width: 50%; /* Adjust width for left section */
}

.contact-left h1 {
    font-size: 80px;
    font-weight: 700;
    background: linear-gradient(270deg, #DF8908 80%, #B415FF 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.contact-left p {
    max-width: 550px;
    color: #D8D8D8;
    font-size: 20px;
    line-height: 35px;
}

.contact-details {
    display: flex;
    flex-direction: column;
    gap: 30px;
    color: #D8D8D8;
    font-size: 22px;
}

.contact-detail {
    display: flex;
    align-items: center;
    gap: 20px;
}

.contact-right {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 30px;
    width: 50%; /* Adjust width for right section */
}

.contact-right label {
    color: #D8D8D8;
    font-size: 22px;
    font-weight: 500;
}

.contact-right input {
    border: none;
    width: 100%; /* Ensure input takes full width */
    max-width: 700px; /* Optional: Set a maximum width */
    height: 78px;
    padding-left: 20px;
    border-radius: 4px;
    background: #32323c;
    color: #A0A0A0;
    font-size: 20px;
    font-family: Outfit;
}

.contact-right textarea {
    width: 100%; /* Ensure textarea takes full width */
    max-width: 700px; /* Optional: Set a maximum width */
    border: none;
    padding: 25px;
    border-radius: 4px;
    background: #32323c;
    color: #D8D8D8;
    font-family: Outfit;
    font-size: 20px;
    box-sizing: border-box;
}

.contact-submit {
    border: none;
    color: white;
    border-radius: 50px;
    font-size: 22px;
    padding: 20px 60px;
    margin-bottom: 50px;
    cursor: pointer;
    transition: 0.3s;
    background: linear-gradient(264deg, #DF8908 -5.09%, #B415FF 106.20%);
}

.contact-submit:hover {
    transform: scale(1.1);
    transition: 0.3s;
}

@media (max-width: 480px) {
    .contact {
        gap: 50px;
        margin: 50px 10px;
        align-items: start;
    }

    .contact-title h1 {
        padding: 0;
        font-size: 50px;
        font-weight: 600;
    }

    .contact-title img {
        width: 130px;
        margin-right: -20px;
    }

    .contact-section {
        flex-direction: column;
        gap: 30px; /* Adjust gap for mobile */
    }

    .contact-left {
        width: 100%; /* Full width for mobile */
    }

    .contact-left h1 {
        font-size: 54px;
    }

    .contact-left p {
        font-size: 18px;
    }

    .contact-details {
        gap: 20px;
    }

    .contact-detail img {
        width: 25px;
    }

    .contact-right {
        width: 100%; /* Full width for mobile */
    }

    .contact-right label {
        font-size: 20px;
    }

    .contact-right input {
        width:95%;
        
    }
    .contact-right textarea {
        margin-bottom: 15px;
        font-size: 18px; /* Adjust margin for mobile */
    }

    .contact-submit {
        font-size: 20px;
        padding: 15px 40px;
    }
}
